import 'slick-carousel/slick/slick.min'

export default {
  init() {
    $('.slider').slick({
      fade: true,
      dots: true,
      infinite: true,
      speed: 300,
      slidesToShow: 1,
    }).on('beforeChange', function(e) {
      e.preventDefault();
      var myVideo = document.getElementById('video');
      myVideo.pause();
      $('#video').parent().removeClass('pause');
    });

    $('.duo-slider').slick({
      fade: true,
      dots: false,
      infinite: true,
      speed: 300,
      slidesToShow: 1,
    });
    var myVideo = document.getElementById('video');

    $('#video').click(function (e) {
      if($(this).parent().hasClass('pause')) {
        $(this).parent().removeClass('pause');
      } else {
        $(this).parent().addClass('pause');
      }
      e.preventDefault();
      playPause();
    });
    function playPause() {
      if (myVideo.paused) {
        myVideo.play();
        myVideo.muted = false
      }
      else
        myVideo.pause();
    }

    $('#slick-1 a').click( function (e) {
      e.preventDefault();
      if ($(this).hasClass('left-arrow')) {
        $('#slick-1').slick('slickPrev');
      } else {
        $('#slick-1').slick('slickNext');
      }
    });
    $('#slick-2 a').click( function (e) {
      e.preventDefault();
      if ($(this).hasClass('left-arrow')) {
        $('#slick-2').slick('slickPrev');
      } else {
        $('#slick-2').slick('slickNext');
      }
    })
  },

  // JavaScript to be fired on all pages, after page specific JS is fired
  finalize() {
  },
};



