import 'bootstrap'
import 'jquery.nicescroll'

export default {
  init() {
    $('.scroll-boxes').niceScroll({
      cursorcolor: 'aquamarine',
      cursorwidth: '11px',
      cursorfixedheight: 70,
      cursoropacitymin: 1,
      horizrailenabled: false,
    });
    $('.scroll').click( function (e) {
      e.preventDefault();
      $('body,html').animate({
        scrollTop: 0,
      }, 800);
    });

    $('.header__dropdown-parent').click( function () {
      $('.header__mobile-dropdown').slideToggle();
      $('.header__dropdown-parent').toggleClass('header__dropdown-parent__active');
      $('.header__mobile-dropdown__inner').slideUp()
  });

    $('.header__mobile-dropdown__a').click( function () {
      $('.header__mobile-dropdown__inner').slideToggle();
      $('.header__mobile-dropdown__a').toggleClass('header__mobile-dropdown__a__active');
    });

    $('#menu-burger').click( function () {
      $('.header__mobile-menu').addClass('header__mobile-menu__active');
    });

    $('.header__close-menu').click(function() {
      closeMenu();
    });

    function closeMenu() {
      $('.header__mobile-menu').removeClass('header__mobile-menu__active');
    }

    if($('body').hasClass('home')) {
      // eslint-disable-next-line no-unused-vars
      let test = 0;
    } else if ($('body').hasClass('simptome')) {
      // eslint-disable-next-line no-unused-vars
      let test = 0;
    } else {
      let box = document.querySelector('.scroll_container');
      box.addEventListener('scroll', function(){
        if (box.scrollTop > 5) {
          $('.arr').css('opacity','0')
        } else {
          $('.arr').css('opacity','1')
        }
      })
      document.querySelector('.nicescroll-cursors').innerHTML = '<div class="counter"><span id="bg" data-scroll="number-group">1</span> <span>/</span> <span class="all-counter"></span></div>';
    }


    //scroll number
    function querySelectorArray(query, root){
      return Array.prototype.slice.call((root || document).querySelectorAll(query));
    }

    var elements = querySelectorArray('[data-scroll]'),
      bg = document.getElementById('bg'),
      container = document.querySelector('.scroll_container');

    elements.forEach(function(color){
      var group = color.getAttribute('data-scroll'),
        sections = querySelectorArray('[data-' + group + ']');

      var lastSection = 1;
      container.addEventListener('scroll', function(){
        sections.forEach(function(section){
          var offset = section.getBoundingClientRect();
          //the magic
          if(offset.top - (window.innerHeight/3) < 0 && offset.top > - offset.height ) lastSection = section.getAttribute('data-' + group + '');
        });
        let high = document.querySelector('.scroll_container').scrollHeight;
        let scroll = document.querySelector('.scroll_container').scrollTop;
        let stop = high - scroll;
        if (stop < 700) {
          bg.innerHTML = all;
        } else {
          bg.innerHTML = lastSection || '';
        }
      })
    });
    let all = $('.scroll_container').children('.point').length;
    $('.all-counter').text(all);
  },

  // JavaScript to be fired on all pages, after page specific JS is fired
  finalize() {
  },
};



