

export default {
  init() {
    $('.line-anim__slider').slick({
      infinite: false,
      slidesToShow: 1,
      slidesToScroll: 1,
    });
    $('.right').click(function(e){
      e.preventDefault();
      $('.line-anim__slider').slick('slickNext');
    });
    $('.left').click(function(e){
      e.preventDefault();
      $('.line-anim__slider').slick('slickPrev');
    });
  },

  // JavaScript to be fired on all pages, after page specific JS is fired
  finalize() {
  },
};



